body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
  --background-color: #EFEEDE;
  --background-color-secondary: #BFBFBF;
  --highlight-color: #d4786a;
  --white-text: whitesmoke;
  --dark-accent: #3D3D3D;
  --passed-color: #79e479;
}


body{
  background-color: #EFEEDE;
  background-color: var(--background-color);
  background-image: linear-gradient(180deg, #FFFFFF 0%, #EFEEDE 100%);
  background-image: linear-gradient(180deg, #FFFFFF 0%, var(--background-color) 100%);


  text-align: center;
  color: #3D3D3D;
  color: var(--dark-accent);
  font-size: 1.25em;

  min-height: 50em;
}


.App{
  width: 90%;
  margin: 0 auto 3em;
}

/* TEST CSS */




#copy-link{
  width: 90%;
  height: 5em;
  margin: 0 auto;
}








textarea{
  font-size: 1.5em;
  width: 100%;
}

input[type = 'checkbox']{
    /* Double-sized Checkboxes */
    -ms-transform: scale(2); /* IE */
    -moz-transform: scale(2); /* FF */
    -webkit-transform: scale(2); /* Safari and Chrome */
    -o-transform: scale(2); /* Opera */
    padding: 10px;
    margin-right: 1em;
}


.tutorial-section h1{
  display: inline;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}


.tutorial-container{
    width: 85%;
    margin: 0 auto;
    text-align: left;
}

.tutorial-banner{
    display: grid;
    grid-template-columns: 4em auto;
    text-align: left;
    margin: 1em auto;

    cursor: pointer;

}

.tutorial-banner-element{
    box-shadow: 2px 2px 5px var(--dark-accent);
}

.tutorial-active-indicator{
    /* margin: 0;
    font-size: 3em; */
    text-align: center;
    font-weight: 900;
    background-color: var(--dark-accent);
    color: var(--white-text);

    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

@-webkit-keyframes slide-open {
    0%{
        color: whitesmoke;
        max-height: 0;
    }

    50%{
        color: var(--dark-accent);
    }

    100%{
        max-height: 250em;
    }
}

@keyframes slide-open {
    0%{
        color: whitesmoke;
        max-height: 0;
    }

    50%{
        color: var(--dark-accent);
    }

    100%{
        max-height: 250em;
    }
}

.content-container{
    box-shadow: 2px 2px 20px var(--dark-accent) inset;
    background-color: whitesmoke;
    border-radius: 8px;
    padding: .25em 0.5em 1em 1.5em;

    -webkit-animation: slide-open 3s forwards;

            animation: slide-open 3s forwards;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}



.tutorial-banner-title{
    padding-left: 1em;
    background-color: var(--background-color-secondary);
}

.tutorial-section-heading{
    background-color: var(--background-color-secondary);
}

.toggle-expand{
    background-color: var(--dark-accent);
    color: var(--white-text);
}



.heading-container{
    margin: 0;
    padding-bottom: 0.25em;
    border-bottom: 1px solid var(--dark-accent);
}

.heading-container h2{
    margin: 1em 0 0;
}

.heading-container hr{
    color: red;
}


.embeded-video{
    width: 100%;
    max-width: 560px;
    margin: 0 auto;
}


#main-nav-banner{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  
    background-color: var(--dark-accent);
    width: 100%;
    margin: 0 auto;
    padding: 2.5em 0;
    text-align: center;
}


#main-nav-banner a{

/* font-size: 1.5em; */
    color: var(--white-text);
    border: 2px solid var(--white-text);

    text-decoration: none;
    font-weight: 600;

    border-radius: 8px;
    padding: .66em;
}

#main-nav-banner a:hover{
    color: var(--highlight-color);
    border-color: var(--highlight-color);
}


input, select, textarea, button{
    border-radius: 4px;
    border: none;

    font-size: 1.5em;
    
    margin: 0.25em 0;

    color: var(--dark-accent);
}

textarea{
    resize: none;
    padding: .66em 0;
    box-shadow: 2px 2px 10px var(--dark-accent) inset;
}





input[type="submit"], #assignment-container, button{
    background-color: rgba(0, 0, 0, 0);
    border: 2px solid black;
    
    padding: 0.25em;
    margin: 1em;
    box-shadow: 2px 2px 8px var(--dark-accent);
    
    cursor: pointer;
}

input[type="submit"]:hover, #assignment-container, button:hover{
    color: var(--highlight-color);
    border: 2px solid var(--highlight-color);
    box-shadow: 5px 5px 20px var(--dark-accent);
}








input[type="text"], input[type="number"]{
    box-shadow: 2px 2px 8px var(--dark-accent);
    text-align: center;
}

select{
    cursor: pointer;
    box-shadow: 2px 2px 8px var(--dark-accent);
}

label{
    font-size: 1.5em;
    margin-bottom: 1em;
}


.copy-text-button{
    font-size: .75em;
}

.assignment-form-section, .section-box{
    padding: 0 0 .66em;
    width: 66%;
    min-width: 300px;
    margin: 1em auto;
    
    border-radius: 10px;
    box-shadow: 2px 2px 10px var(--dark-accent) inset;
    background-color: var(--background-color-secondary);
}

.new-assignment-section-heading, .section-heading{
    margin: 0;
    padding: 0 0 .25em;
    background-color: var(--dark-accent);
    color: var(--background-color);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.in-text-link{
    color: var(--highlight-color);
}
@media only screen and (max-width: 1000px){
    body{
        font-size: 1em;
    }
    
    .nav-button{
        margin: 1em 0;
    }
    #main-nav-banner{
        grid-template-columns: 1fr; 
        grid-gap: 1em;
    }
}

@media only screen and (max-width: 460px){
    body{
        font-size: .75em;
    }
}



#assignment-container{
    background-color: var(--background-color);
    
    padding: 2em;
    width: 93%;
    margin: 2em auto;
    border-radius: 4px;
    border: none;
    box-shadow: 2px 2px 20px var(--dark-accent) inset;
    cursor: auto;

    color: var(--dark-accent);
}

#assignment-container button{
    font-size: 1.5em;
    font-weight: 600;
    color: var(--highlight-color);
    background-color: rgba(255, 255, 255, 0);
    padding: .5em;
    border-radius: 20px;
    border: 3px solid var(--highlight-color);

    cursor: pointer;

}


.nf-assignment-score{
    box-shadow: 2px 2px 20px #a08e7a;
    height: 45em;
}

.nf-answer-score{
    display: hidden;
}


#submit-assignment, #error-navigation-buttons{
    background-color: #DDDDD8;
    color: black;
    /* padding: .66em; */
    margin: 0 auto;
}


#error-navigation-buttons h2, #sign-and-submit-form h2{
    color: var(--white-text);
    background-color: var(--dark-accent);
    padding: .25em 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;

    margin: 0;
}


.assignment-score-passed{
    box-shadow: 2px 2px 40px var(--passed-color);
  }







#submit-assignment h2{
    /* color: var(--highlight-color); */
}
